#my-name {
    display: flex; 
    flex-direction: column; 
    justify-content: center;
    align-items: center;
    margin: 2vh 0 5vh 0;
  }
  
  #my-name-first, #my-name-second {
    margin: 0;
    font-family: "Orbitron", sans-serif;
    font-size: 1.5rem;
    font-weight: 100;    
    letter-spacing: 2px; 
    color: white; 
  }

  #my-name-first {
    padding-bottom: 5px; 
  }

  #arturo-duran {
    font-weight: 800;
    letter-spacing: 3px; 
    text-decoration: none;
    color: var(--blue);
  }

  #arturo-duran:active {
    color: var(--red);
  }