* {
  box-sizing: border-box;
}

.App {
  height: 100vh; 
  display: flex; 
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  background-image: url("./components/images/background-image.png"); 
  background-size: cover;
}


@media (max-height: 720px) {
  .App {
    flex-direction: row; 
    justify-content: center;
  }

  #my-name {
    margin-left: 30px !important; 
  }

  #my-name-first, #my-name-second {
    text-align: center;
}
}
@media (max-height: 380px) {
  .App {
    height: 100%;
  }
}