:root {
    --background-color: #151515; 
    --blue: #5B8FB9;
    --light-grey: #6B728E; 
    --dark-grey: #404258; 
    --red: #EB6440; 
}

#calculator {
    height: 600px;  
    width: 500px; 
    padding: 5px; 
    background-color: var(--background-color);
    display: grid; 
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(6, 1fr);  
    grid-gap: 2px;
    border: 2px solid #3C4048;
    border-radius: 5px; 
}

@media (max-height: 620px) {
    #calculator {
        height: 80%; 
        min-height: 340px;
    }

    #my-name-first, #my-name-second {
        font-size: 1rem !important; 
    }

    button {
        font-size: 1.5rem !important;
        height: auto !important;
    }
    #formula-screen {
        font-size: 1rem !important; 
    }
    #display {
        font-size: 1.5rem !important; 
    }
}

#calculator-display {
    display: grid; 
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 2fr;
    color: white; 
    padding-right: 5px; 
}

.display_props {
    display: flex; 
    font-family: "Orbitron", sans-serif;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    letter-spacing: 2px; 
}
#formula-screen {
    color: #FF8B13; 
    font-size: 1.5rem; 
}

#display {
    color: white; 
    font-size: 2rem; 
}



button {
    font-family: "Saira Condensed", sans-serif;
    color: white; 
    font-size: 2rem; 
    border: none; 
    outline: none; 
    cursor: pointer;
    border-radius: 5px; 
}

button:hover {
    border: 1px solid white; 
}
button:active {
    background-color: #3C4048; 
}
.blue-button {
    background-color: var(--blue);
}

.dark-grey-button {
    background-color: var(--dark-grey);
}

.red-button {
    background-color: var(--red);
}

.light-grey-button {
    background-color: var(--light-grey);
}

#calculator-display {
    grid-column: 1 / 5; 
    grid-row: 1 / 2; 
}

#clear {
    grid-column: 1 / 3; 
    grid-row: 2 / 3; 
}

#divide {
    grid-column: 3 / 4; 
    grid-row: 2 / 3; 
}

#multiply {
    grid-column: 4 / 5; 
    grid-row: 2 / 3; 
}

#subtract {
    grid-column: 4 / -1; 
    grid-row: 3 / 4; 
}

#add {
    grid-column: 4 / -1;
    grid-row: 4 / 5; 
}

#seven {
    grid-column: 1 / 2; 
    grid-row: 3 / 4; 
}

#eight {
    grid-column: 2 / 3; 
    grid-row: 3 / 4; 
}

#nine {
    grid-column: 3 / 4; 
    grid-row: 3 / 4; 
}

#four {
    grid-column: 1 / 2; 
    grid-row: 4 / 5; 
}

#five {
    grid-column: 2 / 3; 
    grid-row: 4 / 5; 
}

#six {
    grid-column: 3 / 4; 
    grid-row: 4 / 5; 
}

#one {
    grid-column: 1 / 2; 
    grid-row: 5 / 6; 
}

#two { 
    grid-column: 2 / 3; 
    grid-row: 5 / 6; 
}

#three {
    grid-column: 3 / 4; 
    grid-row: 5 / 6; 
}

#zero {
    grid-column: 1 / 3; 
    grid-row: 6 / -1; 
}

#decimal {
    grid-column: 3 / 4; 
    grid-row: 6 / -1; 
}

#equals {
    grid-column: 4 / -1; 
    grid-row: 5 / -1; 
}